



















import Vue from "vue";
import Component from "vue-class-component";

@Component({
  props: {
    id: {
      required: true,
      type: Number
    },
    title: {
      required: true,
      type: String
    },
    subTitle: {
      type: String
    },
    preview: {
      required: true,
      type: String
    },
    author: {
      required: true,
      type: String
    },
    createdTime: {
      required: true,
      type: String
    }
  }
})
export default class FeatureComponent extends Vue {
  constructor() {
    super()
  }
  onClick () {
    console.log(this.$props.id)
    this.$router.push(`/article/${this.$props.id}`)
  }
}



